.cwm-tab-nav {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 5px 2px;
  margin-bottom: 10px;
  width: 100%;
  padding: 5px 2px 0 0;
  border-top: 1px solid;
  border-bottom: 1px solid;
}
.cwm-tab-nav::-webkit-scrollbar {
  height: 8px;
}
.cwm-tab-nav::-webkit-scrollbar-thumb {
  border-radius: 4px;
}
.cwm-tab-nav li {
  list-style: none;
  padding: 5px 10px;
  background: #eee;
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-bottom: 0;
  transition: all 0.3s ease-in-out;
  border-radius: 5px 5px 0 0;
  border-top: 1px solid gray;
  border-right: 1px solid gray;
  margin-right: 2px;
}
.cwm-tab-nav li:hover {
  color: white;
}
.cwm-tab-nav li.active {
  color: white;
}
.cwm-tab-nav li.has-settings:after {
  font-family: dashicons;
  font-size: 120%;
  padding-left: 4px;
}
.cwm-tab-nav li.has-settings.setting-show:after {
  content: "\f177";
}
.cwm-tab-nav li.has-settings.setting-hide:after {
  content: "\f530";
}

.color-theme-blue .cwm-tab-nav {
  background: #73cfeb;
  border-color: #2fcfff;
}
.color-theme-blue .cwm-tab-nav::-webkit-scrollbar-thumb {
  background: #b3e6f7;
}
.color-theme-blue .cwm-tab-nav li {
  background: #dcf7ff;
  border-color: #2fcfff;
}
.color-theme-blue .cwm-tab-nav li:hover {
  background: #01a0e9;
}
.color-theme-blue .cwm-tab-nav li.active {
  background: #0073aa;
}
.color-theme-blue .selectize-input {
  background-color: white;
  color: #dcf7ff;
}
.color-theme-blue .selectize-dropdown {
  background-color: white;
}
.color-theme-blue .selectize-dropdown .option.selected,
.color-theme-blue .selectize-dropdown .option.active {
  background-color: #dcf7ff;
  color: #000000;
}
.color-theme-blue .item,
.color-theme-blue [data-value] {
  background-color: white;
  color: #000000;
}
.color-theme-blue .selectize-control.plugin-remove_button .item .remove {
  border-left: none;
}
.color-theme-blue .selectize-control.multi .selectize-input [data-value] {
  background-color: #0073aa;
  background-image: none;
  border: none;
  text-shadow: none;
}

